html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  background: #ffffff;
  color: #1c1c21;
  font-family: 'Inter', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

button {
  display: block;
  border: none;
  background: none;
  outline: none;
}

h1,
ul,
p {
  margin: 0;
}

ul,
button {
  list-style: none;
  padding: 0;
}

.container {
  width: 100vw;
  height: 100vh;
}

.contentOverlay {
  padding-top: 20%;
}

@media (min-width: 768px) {
  .container {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .contentOverlay {
    padding-top: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .contentBlock {
    width: 45%;
  }
}

@media (min-width: 1200px) {
  .contentOverlay {
    justify-content: space-evenly;
  }
  .contentBlock {
    width: 40%;
  }
}

.blankAnswerOverlay {
  width: 100%;
  position: relative;
  margin-bottom: 8px;
}

.blankAnswerOverlay::before {
  position: absolute;
  top: 50%;
  display: block;
  content: '';
  width: 100%;
  height: 1px;
}

.blankAnswerOverlay_inactive::before {
  background: #d0d0d8;
}

.blankAnswer {
  margin: 0 auto;
  position: relative;
  width: 288px;
  height: 56px;
}

.blankAnswer:disabled {
  color: #1c1c21;
}

.blankAnswerSvg {
  width: 288px;
  height: 56px;
}

.blankAnswerSvg_inactive path:hover {
  stroke: #ff8b37;
}

.blankAnswerOverlay_inactive::before :hover {
  background: #ff8b37;
}

.blankAnswerSvg_selected path {
  fill: #fff3eb;
  stroke: #ff8b37;
}

.blankAnswerOverlay_selected::before {
  background: #ff8b37;
}

.blankAnswerSvg_correct path {
  fill: #e6faea;
  stroke: #47d867;
}

.blankAnswerOverlay_correct::before {
  background: #47d867;
}

.blankAnswerSvg_wrong path {
  fill: #fdeeed;
  stroke: #ec6259;
}

.blankAnswerOverlay_wrong::before {
  background: #ec6259;
}

.blankAnswerText {
  max-width: 288px;
  max-height: 56px;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  cursor: pointer;
  padding: 4px 24px;
  display: flex;
  align-items: center;
  font-size: 14px;
  overflow: hidden;
}

.blankAnswerText span {
  text-transform: uppercase;
  font-weight: 600;
  color: #ff8b37;
  margin-right: 8px;
}

@media (min-width: 992px) {
  .blankAnswerOverlay {
    width: 50%;
  }
  .blankAnswerOverlay:nth-of-type(3) {
    margin-bottom: 0;
  }
  .blankAnswerOverlay:nth-of-type(4) {
    margin-bottom: 0;
  }
}
@media (min-width: 1200px) {
  .blankAnswer {
    width: 373px;
    height: 72px;
  }
  .blankAnswerSvg {
    width: 373px;
    height: 72px;
  }

  .blankAnswerText {
    max-width: 373px;
    max-height: 72px;
    padding: 8px 32px;
    font-size: 20px;
  }
}

@media (min-width: 1920px) {
  .blankAnswerOverlay {
    width: 40%;
  }
}

.blankGradeOverlay {
  width: 100%;
  position: relative;
  margin-bottom: 8px;
}

.blankGradeOverlay:first-of-type {
  margin-bottom: 0;
}

.blankGradeOverlay::before {
  position: absolute;
  top: 50%;
  display: block;
  content: '';
  width: 100%;
  height: 1px;
}

.blankGradeOverlay_disactive::before,
.blankGradeOverlay_normal::before {
  background: #d0d0d8;
}
.blankGradeOverlay_active::before {
  background: #ff8b37;
}

.blankGrade {
  margin: 0 auto;
  position: relative;
  width: 240px;
  height: 32px;
}

.blankGradeSvg {
  width: 240px;
  height: 32px;
}

.blankGradeSvg_active path {
  stroke: #ff8b37;
}

.blankGradeText {
  max-width: 240px;
  max-height: 32px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  cursor: pointer;
  display: flex;
  align-items: center;
  font-size: 14px;
  overflow: hidden;
}

.blankGradeText_active {
  color: #ff8b37;
}

.blankGradeText_disactive {
  color: #d0d0d8;
}

@media (min-width: 1200px) {
  .blankGrade {
    height: 40px;
  }

  .blankGradeSvg {
    height: 40px;
  }
}

.boardGrade {
  width: 100%;
  display: flex;
  flex-direction: column-reverse;
}

.gameContainer {
  width: 100vw;
  min-height: 100vh;
}

.gameContentBlock {
  width: 100vw;
  min-height: 100vh;
  padding: 18% 0 10%;
  position: relative;
  background-color: #f5f5f7;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.gameQuestion {
  margin: 0;
  padding: 0 16px;
  text-align: center;
  font-weight: 600;
  font-size: 18px;
  margin-bottom: 10%;
}

.boardBlock {
  width: 100vw;
  min-height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f5f5f7;
}

.boardBlock_active {
  display: flex;
}
.boardBlock_hidden {
  display: none;
}

.gameButton {
  position: absolute;
  top: 21px;
  right: 21px;
  background-repeat: no-repeat;
  background-size: contain;
  cursor: pointer;
}

.openBoardButton {
  width: 16px;
  height: 14px;
  background-image: url(/millionaire/static/media/burger.ef43c181.svg);
}

.closeBoardButton {
  width: 13px;
  height: 13px;
  background-image: url(/millionaire/static/media/close.51226034.svg);
}

@media (min-width: 768px) {
  .gameContainer {
    display: flex;
    flex-wrap: nowrap;
  }
  .gameContentBlock {
    width: 65%;
    padding: 10% 0 10%;
  }
  .boardBlock {
    position: relative;
    width: 35%;
    background-color: #fff;
  }
  .boardBlock_hidden {
    display: flex;
  }
  .gameButton {
    display: none;
  }
}

@media (min-width: 992px) {
  .gameContentBlock {
    width: 74%;
    padding: 10% 5%;
  }
  .gameQuestion {
    text-align: left;
    padding: 0;
  }
  .answersBlock {
    display: flex;
    flex-wrap: wrap;
  }
  .boardBlock {
    width: 26%;
  }
}

@media (min-width: 1200px) {
  .gameContentBlock {
    padding: 5% 5%;
  }
  .gameQuestion {
    font-size: 32px;
  }
}

.logo {
  margin: 0 auto;
  width: 196px;
  height: 155px;
  margin-bottom: 10%;
}

@media (min-width: 768px) {
  .logo {
    margin: 0;
    margin-bottom: 0;
  }
}
@media (min-width: 1200px) {
  .logo {
    width: 452px;
    height: 356px;
  }
}

.mainButton {
  position: absolute;
  bottom: 15%;
  left: 50%;
  transform: translateX(-50%);
  width: 288px;
  height: 48px;
  border-radius: 8px;
  background-color: #ff8b37;
  color: #fff;
  font-weight: 600;
  font-size: 14px;
}

.mainButton:hover {
  background-color: #ffac70;
}

.mainButton:active {
  background-color: #e87928;
}

@media (min-width: 768px) {
  .mainButton {
    position: relative;
    bottom: 0;
    left: 0;
    transform: translateX(0);
  }
}

@media (min-width: 992px) {
  .mainButton {
    width: 296px;
    height: 64px;
    border-radius: 12px;
    font-size: 20px;
  }
}

.startContainer {
  background: linear-gradient(
    to bottom right,
    #fff 0%,
    #fff 50%,
    #fff3eb 50%,
    #fff3eb 100%
  );
}

.startTitle {
  margin: 0 auto;
  width: 90%;
  text-align: center;
  font-weight: 600;
  font-size: 32px;
}

@media (min-width: 768px) {
  .startTitle {
    margin: 0;
    width: auto;
    text-align: left;
    margin-bottom: 5%;
  }
}
@media (min-width: 1200px) {
  .startTitle {
    font-size: 56px;
    margin-bottom: 15%;
  }
}

.winContainer {
  background: #f5f5f7;
}
.winTitle {
  font-weight: 600;
  font-size: 18px;
  color: #1c1c21;
  opacity: 0.5;
  text-align: center;
  margin-bottom: 8px;
}
.winEarned {
  margin: 0 auto;
  width: 90%;
  text-align: center;
  font-weight: 600;
  font-size: 32px;
}

@media (min-width: 768px) {
  .winTitle {
    text-align: left;
  }
  .winEarned {
    margin: 0;
    width: auto;
    text-align: left;
    margin-bottom: 5%;
  }
}
@media (min-width: 1200px) {
  .winLogoOverlay {
    margin-right: 15%;
  }
  .winTitle {
    font-size: 32px;
  }
  .winEarned {
    width: 600px;
    font-size: 56px;
    margin-bottom: 15%;
  }
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

